body {
  margin: 0;
  padding: 0;
  background-color: #1e1e1e;
  font-family: 'Inter', sans-serif;
  color: #e0e0e0;
  width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

.container {
  width: 90%;
  margin: auto;
  max-width: 1200px;
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (min-width: 768px) {
    width: 85%;
  }

  @media (min-width: 1024px) {
    width: 75%;
  }
}

/* Responsive Grid */
.grid-layout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;

  @media (min-width: 768px) {
    gap: 2rem;
  }

  @media (min-width: 1024px) {
    gap: 2.5rem;
  }
}