.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 12vh 2vw;
  max-width: 75vw;
  margin: auto;

  /* üîπ T√çTULO "Sobre M√≠" con color azul FIJO */
  &__title {
    font-family: 'Press Start 2P', cursive;
    font-size: 2rem;
    color: #007bff; // üî• Azul fijo, sin efecto de sombra ni animaci√≥n
    font-weight: bold;
    margin-bottom: 5vh;
    letter-spacing: 0.3vw;
    text-align: center;
  }

  &__text {
    background: rgba(255, 255, 255, 0.1);
    padding: 4vh;
    border-radius: 1vh;
    flex: 1 1 42vw;
    max-width: 42vw;
    min-height: 10vh;
    text-align: left;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;

    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
    }

  }

  &__highlight {
    font-weight: bold;
    color: #ffcc00; /* üî• Amarillo llamativo */
  }

  /* üî• BOTONES DENTRO DEL CUADRO */
  &__links {
    margin-top: 2vh;
    display: flex;
    gap: 2vw;
    justify-content: center;
    width: 100%;
  }

  &__button {
    display: flex;
    align-items: center;
    background: #007bff;
    color: white;
    padding: 1vh 2vw;
    border-radius: 1vh;
    text-decoration: none;
    font-size: 1rem;
    font-weight: bold;
    transition: background 0.3s ease, transform 0.3s ease;

    &:hover {
      background: #ffcc00;
      transform: scale(1.05);
    }

    .icon {
      margin-right: 0.8vw;
      font-size: 1.2rem;
    }
  }


  @media (max-width: 768px) {
    max-width: 100vw;
    padding: 4vh 5vw;

    &__title {
      font-size: 1.8rem;
    }

    &__text {
      font-size: 1rem;
      max-width: 100vw;
    }

    &__links {
      flex-direction: column;
      gap: 1vh;
      width: 100%;
    }

    &__button {
      font-size: 0.9rem;
      padding: 1vh 4vw;
    }
  }
}
