@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.topbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 8vh;
  display: flex;
  background-color: black;
  align-items: center;
  justify-content: space-between;
  padding: 0 3vw;
  z-index: 100;

  /* üî• Clase aplicada cuando el usuario hace scroll */
  &.scrolled {
    background-color: #000000 !important; // üî• Se mantiene s√≥lido
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.6);
  }

  /* üî• Contenido centrado */
  &__content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  /* üî• Men√∫ centrado */
  &__links {
    flex: 1;
    display: flex;
    justify-content: center;
    gap: 3vw;

    a {
      font-family: 'Press Start 2P', cursive;
      font-size: 1.4vh;
      color: #007bff; // üî• Azul llamativo igual que "Sobre M√≠"
      text-decoration: none;
      transition: color 0.3s ease, text-shadow 0.3s ease, transform 0.3s ease;
      text-shadow: 0px 3px 10px rgba(0, 123, 255, 0.7);

      &:hover {
        color: #ffcc00; // üî• Cambio de color al pasar el cursor
        text-shadow: 0px 3px 15px rgba(255, 204, 0, 0.9);
        transform: scale(1.05);
      }
    }

    span {
      color: rgba(255, 255, 255, 0.3);
    }
  }

  /* üî• Iconos alineados a la derecha */

  /* üìå Media Queries */
  @media (max-width: 1024px) {
    height: 10vh;
    padding: 0 2vw;

    &__links {
      gap: 2vw;

      a {
        font-size: 1.3vh;
      }
    }
  }

  @media (max-width: 768px) {
    flex-direction: row;
    height: auto;
    padding: 2vh 3vw;

    &__links {
      gap: 1.5vh;
    }

    &__icons {
      order: 2;
      font-size: 1.8vh;
      gap: 2vw;
    }
  }

  @media (max-width: 480px) {
    flex-direction: row;
    justify-content: space-between;
    height: auto;
    padding: 2vh 3vw;

    &__links {
      gap: 1vh;
    }

    &__icons {
      font-size: 1.5vh;
      gap: 2vw;
    }
  }
}
