.experience {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5vh;
  padding: 0vh 2vw;

  &__title {
    font-family: 'Press Start 2P', cursive;
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 6vh;
    margin-top: 0vh;
    letter-spacing: 0.3vw;
    color: dodgerblue;
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4vh 2vw;
    width: 100%;
    max-width: 90vw;
  }

  &__item {
    background: rgba(255, 255, 255, 0.1);
    padding: 4vh;
    border-radius: 1vh;
    flex: 1 1 42vw;
    max-width: 42vw;
    min-height: 10vh;
    text-align: left;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;

    &:hover {
      transform: translateY(-5px);
    }

    @media (max-width: 1024px) {
      flex: 1 1 48vw;
      max-width: 48vw;
    }

    @media (max-width: 768px) {
      flex: 1 1 85vw;
      max-width: 85vw;
    }

    @media (max-width: 480px) {
      flex: 1 1 95vw;
      max-width: 95vw;
    }
  }

  &__experience-title {
    font-size: 1rem;
    font-weight: bold;
    text-align: left;
    margin-bottom: 1vh;
    color: #34eb37;
    font-family: 'Press Start 2P', cursive;
  }

  &__experience-description {
    font-size: 0.9rem;
    color: #d0d0d0;
    margin-top: 1.5vh;
  }
}

/* üî• Animaci√≥n para el t√≠tulo resaltado */
@keyframes glowing-text {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
