@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5vh 2vw; // üî• REDUCE el espacio superior
  max-width: 100vw;
  overflow: hidden; // üî• Evita el desbordamiento

  &__title {
    font-family: 'Press Start 2P', cursive;
    font-size: 2rem;
    text-align: center;
    color: dodgerblue;
    font-weight: bold;
    margin-bottom: 4vh; // üî• Reduce la separaci√≥n inferior
    margin-top: 0; // üî• QUITA espacio superior
    padding-top: 0; // üî• Evita cualquier relleno extra
    width: 100%;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    width: 100vw;
    overflow: hidden;
    position: relative;
  }

  &__content {
    display: flex;
    gap: 3vw;
    width: fit-content;
    animation: scroll-horizontal 20s linear infinite;
  }

  &__item {
    background: rgba(255, 255, 255, 0.15);
    padding: 2vh;
    border-radius: 1vh;
    flex: 1 1 20vw;
    max-width: 20vw;
    min-height: 10vh;
    text-align: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);

    &:hover {
      transform: scale(1.05);
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
    }
  }

  &__project-title {
    font-size: 0.9rem;
    font-weight: bold;
    font-family: 'Press Start 2P', cursive;
    color: #34eb37;
    margin-bottom: 1vh;
  }

  &__project-description {
    font-size: 0.8rem;
    font-family: 'Poppins', sans-serif;
    color: #d0d0d0;
    margin-top: 0.5vh;
    padding: 0 1rem;
  }

  @media (max-width: 768px) {
    padding: 6vh 2vw;

    &__title {
      font-size: 1.5rem;
      margin-bottom: 2vh;
    }
  }
}
