@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.lenguajes-bar {
  overflow: hidden;
  padding: 5vh 0; /* üî• Espaciado adecuado */
  white-space: nowrap;
  position: relative;
  width: 100vw;
  max-width: 60vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;

  /* üî• T√≠tulo "Skills" CENTRADO Y AZUL */
  .skills-title {
    font-family: 'Press Start 2P', cursive;
    font-size: 2rem; /* üî• Tama√±o visible */
    color: #007bff; /* üî• Azul sin efectos adicionales */
    font-weight: bold;
    text-align: center;
    width: 100%;
    margin-bottom: 2vh;
  }

  &__contenido {
    display: flex;
    animation: mover 15s linear infinite;
    justify-content: flex-start;
    gap: 1.5vw;
    width: fit-content;
    padding-left: 5vw;
  }

  &__item {
    display: flex;
    align-items: center;
    margin: 0 1.5vw;
    font-size: 1.2rem;
    font-weight: bold;
    color: #2ecc71;
    font-family: 'Press Start 2P', cursive;
  }

  &__icono {
    margin-right: 0.5vw;
    font-size: 2rem;
  }

  &__nombre {
    font-size: 1rem;
    font-family: 'Press Start 2P', cursive;
  }

  /* üìå RESPONSIVO */
  @media (max-width: 1024px) {
    max-width: 75vw;

    .skills-title {
      font-size: 1.8rem;
    }
  }

  @media (max-width: 768px) {
    max-width: 85vw;
    padding: 0vh 0;

    .skills-title {
      font-size: 1.5rem;
    }

    &__contenido {
      animation: mover 12s linear infinite;
    }

    &__item {
      font-size: 1rem;
    }

    &__icono {
      font-size: 1.5rem;
    }

    &__nombre {
      font-size: 0.9rem;
    }
  }
}

/* üî• Animaci√≥n de desplazamiento horizontal */
@keyframes mover {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}
